import React from 'react';
import { Link } from 'react-router-dom';
import { NavLink } from 'react-router-dom';
import ProductListItem from './ProductListItem';
import _ from 'lodash';

class Product extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            bySolution : [
                {
                    imageSrc: "https://image.drjart.com/img/001/1508890487005.png",
                    tag: "#No.1 비비크림",
                    title: "닥터자르트 리쥬비네이팅 뷰티 밤",
                    price: "23200"
                },
                {
                    imageSrc: "https://image.drjart.com/img/001/1509686773012.png",
                    tag: "#No.1 비비크림",
                    title: "세라마이딘 판타지 세럼 세트",
                    price: "42000"
                },
                {
                    imageSrc: "https://image.drjart.com/img/001/1508763311886.png",
                    tag: "#No.1 비비크림",
                    title: "세라마이딘 리퀴드",
                    price: "39000"
                },
                {
                    imageSrc: "https://image.drjart.com/img/001/1509087961415.png",
                    tag: "#No.1 비비크림",
                    title: "닥터자르트 리쥬비네이팅 뷰티 밤",
                    price: "23200"
                },
                {
                    imageSrc: "https://image.drjart.com/img/001/1509092767411.png",
                    tag: "#No.1 비비크림",
                    title: "세라마이딘 판타지 세럼 세트",
                    price: "42000"
                },
                {
                    imageSrc: "https://image.drjart.com/img/001/1508936885386.png",
                    tag: "#No.1 비비크림",
                    title: "세라마이딘 리퀴드",
                    price: "39000"
                },
                {
                    imageSrc: "https://image.drjart.com/img/001/1508890487005.png",
                    tag: "#No.1 비비크림",
                    title: "닥터자르트 리쥬비네이팅 뷰티 밤",
                    price: "23200"
                },
                {
                    imageSrc: "https://image.drjart.com/img/001/1508890487005.png",
                    tag: "#No.1 비비크림",
                    title: "세라마이딘 판타지 세럼 세트",
                    price: "42000"
                },
                {
                    imageSrc: "https://image.drjart.com/img/001/1508890487005.png",
                    tag: "#No.1 비비크림",
                    title: "세라마이딘 리퀴드",
                    price: "39000"
                },
            ],

            byItem : [
                {
                    imageSrc: "https://image.drjart.com/img/001/1508890487005.png",
                    tag: "#No.1 비비크림",
                    title: "더마클리어 마이크로 폼",
                    price: "14400"
                },
                {
                    imageSrc: "https://image.drjart.com/img/001/1508890487005.png",
                    tag: "#No.1 비비크림",
                    title: "더마클리어 마이크로 워터 점보",
                    price: "38000"
                },
                {
                    imageSrc: "https://image.drjart.com/img/001/1508890487005.png",
                    tag: "#No.1 비비크림",
                    title: "컨트롤에이 리퀴드 폼",
                    price: "22000"
                }
            ]
        }
    }


    renderListItem(){

        function getParameterByName(name) {
            var url = window.location.href;
            name = name.replace(/[\[\]]/g, "\\$&");
            var regex = new RegExp("[?&]" + name + "(=([^&#]*)|&|#|$)"),
                results = regex.exec(url);
            if (!results) return null;
            if (!results[2]) return '';
            return decodeURIComponent(results[2].replace(/\+/g, " "));
        }

        let cateSolution = getParameterByName('cateSolution') ? getParameterByName('cateSolution') : "";
        switch (cateSolution){
            case "1":
                return _.map(this.state.bySolution, (info, key) => <ProductListItem key={key} {...info} /> )
            break;

            case "2":
                return _.map(this.state.byItem, (info, key) => <ProductListItem key={key} {...info} /> )
                break;
        }
    }

    render() {
        return (
            <div className="page-shop">
                <header className="global-nav">
                    <h1 className="logo">
                        <Link to="/">koo</Link>
                    </h1>
                    <nav>
                        <NavLink to="/shop/All products" className="nav-link" activeClassName="is-active">all</NavLink>
                        <NavLink to="/shop/by Solution?cateSolution=1" className="nav-link" activeClassName="is-active">by Solution</NavLink>
                        <NavLink to="/shop/by Item?cateSolution=2" className="nav-link nav-product" activeClassName="is-active">by Item</NavLink>
                        <NavLink to="/shop/by Line" className="nav-link" activeClassName="is-active">by Line</NavLink>
                        <NavLink to="/shop/Online Set" className="nav-link" activeClassName="is-active">Online Set</NavLink>
                    </nav>
                </header>
                <div className="content-area">
                    <div className="category-list">
                        <h2>{this.props.match.params.category}</h2>
                        <ul>
                            {this.renderListItem()}
                        </ul>
                    </div>
                    <div className="recommend-list">
                        <h2>닥터자르트 추천상품</h2>
                    </div>
                </div>
            </div>
        );
    }
}

export default Product;


