import React from 'react';
import Visual from './Sections/Visual';
import { Link } from 'react-router-dom';
import { NavLink } from 'react-router-dom';

class Home extends React.Component {
    render() {
        return (
            <main className="home-main">
                <header className="global-nav">
                    <h1 className="logo">
                        <Link to="/">koo</Link>
                    </h1>
                    <nav>
                        <NavLink exact to="/" className="nav-link" activeClassName="is-active">Home</NavLink>
                        <NavLink to="/about" className="nav-link" activeClassName="is-active">about</NavLink>
                        <NavLink to="/shop/by Solution?cateSolution=1" className="nav-link nav-product" activeClassName="is-active">product</NavLink>
                        <NavLink to="/blog" className="nav-link" activeClassName="is-active">blog</NavLink>
                        <NavLink to="/contact" className="nav-link" activeClassName="is-active">contact</NavLink>
                    </nav>
                </header>
                <Visual/>
            </main>
        );
    }
}

export default Home;


