import React from 'react';
import { Link } from 'react-router-dom';

class Aside extends React.Component {
    render() {
        return (
            <aside className="global-aside">
                <div className="bar-default">
                    <div className="nav-section">
                        <div className="nav-icon"><i className="material-icons">add</i></div>
                    </div>
                    <div className="btn-aside-menu" onClick={this.props.handleAside}>
                        <div className="stick"></div>
                        <div className="stick"></div>
                        <div className="stick"></div>
                    </div>
                    <div className="tools">
                        <div className="item search">
                            <i className="material-icons">search</i>
                        </div>
                        <div className="item login">
                            <i className="material-icons">person_outline</i>
                        </div>
                        <div className="item faq">
                            <i className="material-icons">help_outline</i>
                        </div>
                    </div>
                </div>
                <nav className="aside-menu">
                    <h1>koo</h1>
                    <div className="links-page">
                        <Link exact to="/">home</Link>
                        <Link to="/about">about</Link>
                        <Link to="/shop/by Solution?cateSolution=1">work</Link>
                        <Link to="/blog">blog</Link>
                        <Link to="/skill">skill</Link>
                    </div>
                </nav>
            </aside>
        );
    }
}

export default Aside;


